<template>
  <div class="px-12 py-4 bg-grey-lightest">
    <div class="flex flex-col md:flex-row justify-between">
      <h1 @click="age++" class="text-center text-2xl text-grey-darkest p-3">
        {{ msg }}
      </h1>
      <button
        class="border-4 border-grey-lighter rounded-lg p-3 hover:bg-green-lightest md:hover:bg-grey-light"
        @click="showInput = !showInput"
      >
        {{ btn }}
      </button>
    </div>
    <div class="flex flex-col md:flex-row" v-if="showInput">
      <input
        type="text"
        placeholder="Event Name"
        class="text-xl px-6 py-2 outline-none mt-2"
        v-model="name"
      />
      <p
        class="text-xl bg-grey-darkest text-white py-3 px-12 md:ml-8 m-0 text-center mt-2"
        @click="addEvent"
      >
        Add
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "navbar",
  props: {
    msg: String,
    btn: String,
  },
  data() {
    return {
      showInput: false,
      name: "",
    };
  },
  methods: {
    addEvent: function () {
      console.log(this.name);
      this.showInput = false;
      this.$emit("addEvent", this.name);
      this.name = "";
    },
  },
};
</script>

