<template>
  <div class="relative pb-6">
    <navbar msg="Bill Split" btn="Add Event" v-on:addEvent="addEvent($event)" />

    <div class="flex-wrap justify-between mx-6 lg:mx-24">
      <div v-for="(event, key) in events" :key="key" class="mt-3">
        <eventBox class="w-full h-auto lg:m-6 my-3" :eventName="event" />
        <p
          class="-mt-2 float-right text-xl font-bold bg-red-light p-3 text-white my-6"
          @click="deleteEvent(key)"
        >
          Delete
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import navbar from "./components/nav-bar.vue";
import eventBox from "./components/event-box.vue";

export default {
  name: "App",
  components: {
    navbar,
    eventBox,
  },
  data() {
    return {
      events: [],
    };
  },
  methods: {
    addEvent: function (event) {
      this.events.push(event);
    },

    deleteEvent: function (event) {
      console.log(event);
      this.events.splice(event, 1);
    },
  },
};
</script>

